import * as React from 'react';
import { navigate } from 'gatsby';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import { Layout } from '../../components/layout';
import { Link } from '../../components/common/link';
import { Navigation } from '../../components/navigation';
import { Seo } from '../../components/seo';
import { categories } from '../../libs/categories';
import { works } from '../../libs/works';

const Page = (props: any) => {
  const [category, setCategory] = React.useState('hotel');

  const datas = works;

  React.useEffect(() => {
    const param = new URLSearchParams(props.location.search);
    const paramCategory: string | null = param.get('category');
    paramCategory && setCategory(paramCategory);

    setTimeout(() => {
      const scrollY: number = Number(
        window.localStorage.getItem('scrollY') || 0
      );
      window.scroll(0, scrollY);
    }, 100);
  }, [props.location.search]);

  return (
    <Layout>
      <Navigation />
      <Box
        py={10} // header 分
      >
        <Box
          mb={4} // thumbnail の spacing 分
          sx={{
            display: 'flex',
            justifyContent: 'center'
          }}
        >
          {categories.map(({ label, id }, i) => {
            // @TODO all の時に本当は /works/ にしたい
            const link = `/works/${id === 'all' ? '?category=all' : `?category=${id}`}`;

            const isCurrent =
              (id === 'all' && category === null) || id === category;

            return (
              <Link
                key={i}
                href={link}
                sx={{
                  mx: {
                    md: 4,
                    sm: 2,
                    xs: 1
                  },
                  textDecoration: isCurrent && 'underline',
                  pointerEvents: isCurrent && 'none'
                }}
              >
                {label}
              </Link>
            );
          })}
        </Box>
        <Grid container direction='row' spacing={2}>
          {category !== undefined &&
            datas
              .filter(
                ({ category: thumbnailCategory }: { category: string[] }) => {
                  // ページカテゴリが何か指定されているかつ
                  // allではない場合は比較し、そうでなければ all とみなして true を返す
                  if (category && category !== 'all') {
                    // @TODO: arrayとstringが混合している
                    // カテゴリが複数指定されていたら includes 判定
                    if (Array.isArray(thumbnailCategory)) {
                      return thumbnailCategory.includes(category);
                    } else {
                      return thumbnailCategory === category;
                    }
                  } else {
                    return true;
                  }
                }
              )
              .map(
                (
                  { name, link }: { name: string; link: string },
                  i: number
                ): React.JSX.Element => {
                  link += `/${category ? `?category=${category}` : ''}`;

                  return (
                    <Grid
                      key={i}
                      size={{
                        lg: 2,
                        md: 3,
                        sm: 3,
                        xs: 6
                      }}
                      sx={{
                        transition: 'all 250ms ease-out'
                      }}
                    >
                      <Link
                        href={`/works/${link}`}
                        sx={{
                          display: 'block',
                          width: '100%',
                          height: '100%'
                        }}
                        onClick={e => {
                          e.preventDefault();
                          window.localStorage.setItem(
                            'scrollY',
                            String(window.scrollY)
                          );
                          navigate(link);
                        }}
                      >
                        <img
                          src={`https://cdn.yuheiono.com/images/${name}_00.webp`}
                          width={400}
                          height={400}
                          style={{
                            display: 'block',
                            width: '100%',
                            height: 'auto',
                            backgroundColor: '#ddd'
                          }}
                        />
                      </Link>
                    </Grid>
                  );
                }
              )}
        </Grid>
      </Box>
    </Layout>
  );
};

export default Page;

export const Head = (): React.JSX.Element => <Seo title='WORKS' />;
